.header {
    height: 79px;
    background: #3D3D3D;
    position: relative;
    display: flex;
    align-items: center;
    z-index: 100
}

.header__wrp {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.popup {
    display: none;
    position: absolute;
}

.header__logo {
    flex-basis: 75%
}

.header__dollar {
    flex-basis: 10%;
}

.btn-balance {
    flex-basis: 15%
}

.header__dollar {
    background: none;
    box-shadow: none;
    border: none;
    width: 39px;
    height: 39px;
    transition: all .3s;

    &:hover {
        opacity: .7;
    }
}

.btn-balance {
    background: $green;
    transition: all .3s;
    width: 180px;
    height: 42px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    font-size: 13px;
    font-style: normal;
    font-weight: 700;
    line-height: 17px;
    letter-spacing: 0em;
    text-transform: uppercase;
    border: none;
    box-shadow: none;

    &:hover {
        background: lighten($green, 20%);
    }
}

@media only screen and (max-width : 994px) {
    .header__logo {
        flex-basis: 65%
    }

    .header__dollar {
        flex-basis: 10%;
    }

    .btn-balance {
        flex-basis: 25%
    }
}

@media only screen and (max-width : 578px) {
    .header__logo {
        flex-basis: 32%;
        width: 89px;

    }

    .header__dollar {
        flex-basis: 29%;

        img {
            width: 28px;
            height: 28px;
        }
    }

    .btn-balance {
        flex-basis: 39%;
        background: transparent;
        transition: all .3s;
        width: 100%;
        height: auto;
        display: flex;
        justify-content: center;
        align-items: center;
        color: #FCFCFC;
        font-size: 10px;
        font-style: normal;
        font-weight: 400;
        line-height: 13px;
        letter-spacing: 0em;
        text-align: left;
        text-transform: uppercase;

    }


}
