.wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.form-container {
    display: flex;
    flex-grow: 1;


}

.form-img {
    background: url('../img/bg.jpg');
    width: 51.57%;
    height: 719px;

    img {
        width: 100%;
        object-fit: cover;
    }
}

.form {
    width: 48.43%;
    padding: 142px 40px 20px 56px;

    &__title {
        font-size: 42px;
        font-style: normal;
        font-weight: 700;
        line-height: 54px;
        letter-spacing: 0em;
        text-align: left;
        margin-bottom: 21px;


    }

    &__btn {
        width: 247px;
        height: 76px;
        background: $green;
        mix-blend-mode: normal;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        color: #fff;
        border: none;
        margin: 60px 0 25px 0;
        font-size: 24px;
        font-style: normal;
        font-weight: 700;
        line-height: 31px;
        letter-spacing: 0em;
        transition: all .3s;
        cursor: pointer;

        &:hover {
            box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.25);

        }


    }

    &__info {
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 18px;
        letter-spacing: 0em;
        text-align: left;
        padding-right: 129px;

        a {
            color: #000;
        }

    }

}

.mobile-hidden {
    display: none;
}


@media only screen and (max-width : 906px) {
    .form-img {

        height: auto;
        background-size: contain;
        background-repeat: no-repeat;
    }

    .form {
        width: 48.43%;
        padding: 0px 20px 20px 20px;

        &__title {
            font-size: 30px;
            line-height: 34px;

        }

        &__info {
            padding-right: 0;

        }

        &__btn {
            width: 136px;
            height: 45px;
            margin: 10px 0 13px 0;
            font-size: 14px;
            line-height: 22px;
            box-shadow: none;

            &:hover {
                background: lighten($green, 20%)
            }
        }
    }
}


@media only screen and (max-width : 612px) {
    .mobile-hidden {
        display: block;
    }

    .form-img {
        display: none;
    }

    body {
        background: #383838;
    }

    .wrapper {
        height: auto;
    }

    .form {
        padding: 49px 22px 22px 22px;
        width: 100%;
        position: relative;

        &__title {
            font-size: 20px;
            font-style: normal;
            font-weight: 700;
            line-height: 26px;
            letter-spacing: 0em;
            text-align: center;
            color: #fff;
            margin-bottom: 13px;
        }

        &__btn {
            width: 156px;
            height: 39px;
            margin: 30px 0 13px 0;
            font-size: 17px;
            line-height: 22px;
            box-shadow: none;

            &:hover {
                background: lighten($green, 20%)
            }
        }

        &__info {
            font-size: 9px;
            font-style: normal;
            font-weight: 400;
            line-height: 10px;
            letter-spacing: 0em;
            text-align: left;
            color: #fff;
            padding-right: 0;
            width: 156px;

            a {
                color: #fff;
            }

        }
    }

    .form__mobile-logo {
        display: flex;
        justify-content: center;
        margin-bottom: 0px;
    }

    .btn-wrp {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        align-items: flex-end;
    }

    .form__mobile-phone {
        position: absolute;
        left: 3px;
        bottom: -125px;
    }

}
