.input-block {
    position: relative;
}

.input-block:not(:last-of-type) {
    margin-bottom: 26px;
}

.input-block,
.input:focus,
    {
    padding: 10px 0;
}

.input:focus + .input-placeholder,
    {
    opacity: 1;
    top: 7px;
}

.input-placeholder {
    position: absolute;
    top: 0px;
    left: 0px;
    opacity: 0;
    font-size: 12px;
    color: #bebebe;
    -webkit-transition: all 0.2s ease-in-out;
    -o-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
}


.input {
    display: block;
    width: 100%;
    height: 54px;
    min-width: 1px;
    border: none;
    border-bottom: 1px solid $green;
    -webkit-transition: all 0.1s linear;
    -o-transition: all 0.1s linear;
    transition: all 0.1s linear;
    background: transparent;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 26px;
    letter-spacing: 0em;
    text-align: left;
    color: $black;
    padding-left: 3px;

    &::placeholder {
        color: $black;
    }

}




.input:focus::-webkit-input-placeholder {
    opacity: 0;
    color: transparent;
}

.input:focus::-moz-placeholder {
    opacity: 0;
    color: transparent;
}

.input:focus:-ms-input-placeholder {
    opacity: 0;
    color: transparent;
}

.input:focus::-ms-input-placeholder {
    opacity: 0;
    color: transparent;
}

.input:focus::placeholder {
    opacity: 0;
    color: transparent;
}

@media only screen and (max-width : 612px) {
    .input {
        font-size: 14px;
        color: #fff;
        height: 55px;
        padding-left: 0;
        padding-top: 17px;
        margin-bottom: 6px;

        &::placeholder {
            color: #fff;
        }
    }

    .input-block:not(:last-of-type) {
        margin-bottom: 0px;
    }

    .input-block {
        padding: 0;
    }

    .input-placeholder {

        font-size: 10px;
        color: #bebebe9e;

    }


}
