.main {
    background: url('../img/bg2.jpg') center top;
    background-size: cover;
    background-repeat: no-repeat;
    height: 100vh;
    margin-top: -79px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    z-index: 90;

}

.main-wrapper {
    display: flex;
    justify-content: space-between;
    margin-top: -79px
}

.block-title {
    font-size: 23px;
    font-style: normal;
    font-weight: 700;
    line-height: 30px;
    letter-spacing: 0em;
    text-align: left;
    color: $green;
    text-transform: uppercase;
    margin-bottom: 34px;

}

.custom-group {
    background: #707070;
    border: 1px solid $green;
    height: 56px;
    width: 392px;
    font-size: 20px;
    font-style: normal;
    font-weight: 700;
    line-height: 26px;
    letter-spacing: 0em;
    text-align: left;
    color: #fff;
    padding-left: 19px;
    position: relative;
    display: flex;
    align-items: center;
    margin-bottom: 34px;


    .select__icon {
        margin-left: 10px;
        margin-top: 10px;
    }
}

.select__body {
    display: none;
}


.stat__btns {
    display: flex;
    justify-content: flex-start;
    margin-bottom: 30px;

}

.stat__btn {
    width: 122px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
    box-shadow: none;
    font-size: 13px;
    font-style: normal;
    font-weight: 700;
    line-height: 17px;
    letter-spacing: 0em;
    text-transform: uppercase;
    color: #fff;
    margin-right: 13px;
    background: $green;

    &:hover {
        background: lighten($green, 20%);
    }

    &:last-child {
        margin-right: 0;
    }

}

.stat__info {
    padding: 14px 16px 23px 16px;
    background: #707070;
    border: 1px solid $green;
    display: flex;
    justify-content: space-between;
    height: 83px;
}

.sblock {
    flex-basis: 33.3%;
}

.sblock__title {
    font-size: 13px;
    font-style: normal;
    font-weight: 700;
    line-height: 17px;
    letter-spacing: 0em;
    text-align: left;
    color: #fff;
    margin-bottom: 13px;
    text-align: center;
}

.sblock__nfo {
    font-size: 11px;
    font-style: normal;
    font-weight: 700;
    line-height: 14px;
    letter-spacing: 0em;
    color: $green;
    text-align: center;
}



@media only screen and (max-width : 828px) {


    .main-wrapper {
        flex-wrap: wrap;
        margin-top: 20px;

    }

    .taxes {
        flex-basis: 100%;
    }

    .stat {
        flex-basis: 100%;
    }

    .stat__btns {
        flex-basis: 33%;
    }

    .custom-group {
        width: 100%;

    }
}

@media only screen and (max-width : 576px) {
    .main {
        margin-top: -79px;
        padding-top: 40px
    }

    .stat__btns {
        margin-bottom: 19px;
    }

    .stat__btn {
        width: 33.3%;
        height: 24px;
        font-size: 10px;
        font-style: normal;
        font-weight: 700;
        line-height: 13px;
        letter-spacing: 0em;

    }

    .custom-group {
        height: 36px;
        font-size: 12px;
        line-height: 15px;
        padding-left: 9px;
        margin-bottom: 16px;


        .select__icon {
            margin-left: 10px;
            margin-top: 10px;

            img {
                width: 15px;
                height: 15px;
            }
        }
    }

    .block-title {
        display: block;
        text-align: center;
    }

    .taxes {
        margin-bottom: 41px;
    }

    .sblock__nfo {
        font-size: 11px;
    }

    .stat__info {
        padding: 12px 6px 12px 6px;
        background: #707070;
        border: 1px solid $green;
        display: flex;
        justify-content: space-between;
        height: 69px;
    }

    .sblock__title {
        font-size: 11px;
        margin-bottom: 3px;

    }

    .st-title {
        margin-bottom: 46px;
    }


}
